.times {
    width: 100%;
    display: flex;
    display: -webkit-flex;
    justify-content: center;
  }
  
  .times-content {
    display: inline-block;
    margin: 0 auto;
    width: 280px;
    height: 280px;
    margin-top: 25px;
    display: flex;
    display: -webkit-flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 5px solid #4ebfc9;
    border-radius: 50%;
  }
  
  #timer-label {
    font-size: 1.5rem;
    font-weight: 700;
  }
  
  #time-left {
    font-size: 5rem;
    font-weight: 700;
  }

  .controller {
    display: flex;
    display: -webkit-flex;
    justify-content: center;
    margin-top: 15px;
  }
  
  .controller button {
    font-size: 1.1rem;
    font-weight: 700;
    border: 1px solid #4ebfc9;
    background-color: transparent;
    color: #fff;
    border-radius: 6px;
    outline: none;
    width: 85px;
    height: 35px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    -webkit-transition: all 0.25s ease-out;
    -moz-transition: all 0.25s ease-out;
    transition: all 0.25s ease-out;
    margin: 15px;
    text-transform: uppercase;
  }
  
  .controller button:hover {
    cursor: pointer;
    background-color: #4ebfc9;
  }

 body {
    background-color: #6e00f5;
